import React, { useState } from 'react'
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from 'yup'

import { IStudent } from '../../../types/types'

import Button2 from '../../../components/Button2/Button2'
import ToastSucces from '../../../components/ToastSucces/ToastSucces';

import avatar from '../../../shared/assets/1.jpg'
import edit from '../assets/Edit.svg'

import { Container, Header, Grid, DropdownContainer, DropdownText, Select, Text, TextareaContainer, Textarea, Value, Img, ProfilePic, Button, FileInput, PicBtn, PicHeader, ImgEdit, Edit, FloatedBtn, StyledInput } from './styles'

const schema = yup.object().shape({
    file: yup
        .mixed()
        .test("fileSize", " .    5Mb.", (value) => {
            return value && value[0] && value[0].size <= 5000000;
        }),
    name: yup
        .string()
        .matches(/^([^0-9]*)$/, "    ")
        .required("   "),
    surname: yup
        .string()
        .matches(/^([^0-9]*)$/, "    ")
        .required("   "),
    date: yup
        .date()
        .test("date", "Wrong birthday", (value:any) => {
            const today = new Date()
            return value < today
        }),
    information: yup
        .string()
        .max(99, '   - 99 '),
    about: yup
        .string()
        .max(300, '   - 300 '),
})

const AboutMeForm = () => {
    const { register,handleSubmit, formState: { errors }} = useForm({
        resolver: yupResolver(schema)
    });

    const [succes, setSucces] = useState<boolean>(false)
    const [aboutValue, setAboutValue] = useState<string>('');
    const [informationValue, setInformationValue] = useState<string>('');

    const onSubmit = handleSubmit((data) => {
        console.log(data)
        setDisabledBtn(false)
        setSucces(true)
    });

    const [isDisabled, setDisabledBtn] = useState<boolean>(false)

    const fileLoader = () => {
        const input = document.querySelector('#fileLoader');
        // @ts-ignore
        input.click()
    }

    return (
        <Container onSubmit={onSubmit}>
            <Header>
                <h3> </h3>
            </Header>
                <Edit>
                    <ProfilePic>
                        <Img src={avatar} alt='avatar'/>
                        <PicBtn>
                            <PicHeader> </PicHeader>
                            <Button type='button' onClick={fileLoader}><ImgEdit src={edit} alt='edit'/> </Button>
                        </PicBtn>
                    </ProfilePic>
                    <FileInput type='file' id='fileLoader' accept=".png, .jpg, .jpeg" 
                        {...register("file", {
                            onChange: (e) => console.log(e.target.files[0].name)
                        })}
                    />
                    { !isDisabled && <Button2 type='button' onClick={() => setDisabledBtn(true)}></Button2>}
                </Edit>
            <Grid>
                <StyledInput 
                    {...register("name")} 
                    label=''
                    disabled={!isDisabled}
                    errors={errors.name}
                    errorMsg={errors.name && errors.name.message}
                />
                <StyledInput 
                    {...register("surname")} 
                    label=''
                    disabled={!isDisabled}
                    errors={errors.surname}
                    errorMsg={errors.surname && errors.surname.message}
                />
                <StyledInput 
                    {...register("date")} 
                    label=' '
                    disabled={!isDisabled}
                    errors={errors.date}
                    errorMsg={errors.date && errors.date.message}
                />
                <DropdownContainer>
                    <DropdownText></DropdownText>
                    <Select disabled={!isDisabled} {...register("city")}>
                        <option value=''>
                            
                        </option>
                        <option value=''>
                            
                        </option>
                        <option value=''>
                            
                        </option>
                    </Select>   
                </DropdownContainer>
                <DropdownContainer>
                <DropdownText></DropdownText>
                    <Select disabled={!isDisabled} {...register("sex")}>
                        <option value=''>
                            
                        </option>
                        <option value=''>
                            
                        </option>
                    </Select>   
                </DropdownContainer>
                <DropdownContainer>
                    <DropdownText></DropdownText>
                    <Select disabled={!isDisabled} {...register("pet")}>
                        <option value=''>
                            
                        </option>
                        <option value=''>
                            
                        </option>
                    </Select>                      
                </DropdownContainer>
            </Grid>
            <TextareaContainer>
                <Text> </Text>
                <Textarea small
                    disabled={!isDisabled} {...register("information")}
                    placeholder="    ..."
                    value={informationValue}
                    onChange={(e) => {
                        setInformationValue(e.target.value)
                    }}    
                > 
                </Textarea>
                { isDisabled && <Value>{informationValue.length}/99</Value>}
            </TextareaContainer>
            <TextareaContainer>
                <Text> </Text>
                <Textarea
                    disabled={!isDisabled} {...register("about")}
                    placeholder="    ..."
                    value={aboutValue}
                    onChange={(e) => {
                        setAboutValue(e.target.value)
                    }}    
                > 
                </Textarea>
                { isDisabled && <Value>{aboutValue.length}/300</Value>}
            </TextareaContainer>
            { isDisabled && <FloatedBtn type='submit'> </FloatedBtn>}
            <ToastSucces 
                title=""
                body="  !"
                show={succes} 
                setShow={setSucces}
            ></ToastSucces>
        </Container>
    )
}

export default AboutMeForm